import axios from '@/libs/axios.js'
import utils from '@/auth/utils'
const { setData, clearData,isUserLoggedIn } = utils

export default {
    namespaced: true,
    state:{
        arquivos:[],
        loading:false,
        progress_panel:false,
        progresso:0,
        history:[]
 
    },
    getters:{
        todo:({ history }) => {
            
            return history
        },
        panel(state){
            return state.progress_panel
        },
        loading_state(state){
            return state.loading
        },
        progresso_total(state){
            return state.progresso
        },
        arquivosGetter({ arquivos }){
            return arquivos
        },


    },
    mutations: {
        COMMIT_UPLOAD_HISTORY(state, value){
            state.history = value
        },
        COMMIT_PROGRESSO(state, value){
            state.progresso = value
        },
        COMMIT_PANEL(state, value){
            state.progress_panel = value
        },
        COMMIT_LOADING(state, value){
            state.loading = value
        },
        COMMIT_ARQUIVOS(state, files){
            state.arquivos = files
        },


    },
    actions:{
        async deleteUploadHistory({ commit,dispatch,state }, item){
            return new Promise(async (resolve,reject) => {
                try {
                    commit('COMMIT_LOADING', true);
                    
                    
                    const response = (await axios.delete('/carregamento',{ params:item })).data

                    console.log(response)
                    await dispatch("getUploadHistoryByIdUsuario")
                    resolve(response)
                    
                    commit('COMMIT_LOADING', false);
                } catch (error) {
                    
                    commit('COMMIT_LOADING', false);

                    reject(error)
                    
                }
            })
        },
        downloadFileUploaded({ commit,dispatch,state }, item){
            return new Promise(async (resolve,reject) => {
                try {
                    
                    
                    commit('COMMIT_PROGRESSO', 0);
                    commit('COMMIT_LOADING', true);

                    window.open(`${axios.getUri()}/carregamento/download?caminho=${item.caminho}`)

                    resolve(item);

                    commit('COMMIT_LOADING', false);

                } catch (error) {

                    commit('COMMIT_LOADING', false);

                    reject(error)
                    
                }
            })
        },
        async prepararArquivos({ commit }, files){
            return new Promise((resolve,reject) => {
                try {
                    
                    commit('COMMIT_ARQUIVOS', files)

                    resolve( 'preparado para enviar' )
                } catch (error) {

                    reject(error)   
                }
            })
        },
        async tentarCarregar({commit, dispatch,state}, file ){
            return new Promise(async (resolve, reject) => {
                try {
                    commit('COMMIT_PROGRESSO', 0)
                    commit('COMMIT_PANEL', true)
                    commit('COMMIT_LOADING', true)
                    
                    const files = state.arquivos
    
                    for (var i = 0; i < files.length; i++) {
                
                        const file = files[i];
                      
                    await dispatch("carregarUm", file)
                    await dispatch("getUploadHistoryByIdUsuario")
                    
                }
                    
                    commit('COMMIT_PANEL', false)
                    commit('COMMIT_LOADING', false)
                    resolve(`foi carregado no total: ${files.length} arquivo(s)`)
                } catch (error) {
                    commit('COMMIT_PANEL', false)
                    commit('COMMIT_LOADING', false)
                    reject(error)
                    
                }
            })
        },
        async carregarUm({ commit , dispatch }, file ){
            return new Promise(async (resolve, reject) => {
        
                const formData = new FormData()

                formData.append("file",file)
                try {
        
                  const data = (await axios.post('/carregamento', formData ,
                   {
                    onUploadProgress:(p) => {
                    const percentagem = ((p.loaded / p.total) * 100 | 0)
                    commit('COMMIT_PROGRESSO', percentagem) 
                  }}))
                    
                    resolve(data)
        
        
                } catch (error) {
        
                  reject(error)
        
                }
        
              })
            

        },

        async getUploadHistoryByIdUsuario({ commit , dispatch }){
            return new Promise(async (resolve,reject) => {
                try {
                    
                    const item = (await axios.get('/carregamento',{ })).data

                    if(!item.response) throw item.msg
                    commit('COMMIT_UPLOAD_HISTORY', item.query)
                    resolve(item)

                    
                } catch (error) {

                    reject(error)
                }
                    
                })
        }

        

    }
}