import axios from '@/libs/axios.js'
import utils from '@/auth/utils'
import toastify from 'toastify-js'
const { setData, clearData,isUserLoggedIn } = utils



export default {
    namespaced: true,
    state:{
 
    },
    getters:{

    },
    mutations: {


    },
    actions:{
        registrar({ }, form){
            return new Promise(async (resolve,reject) => {
                try {
                    
                    const data = ((await axios.post('/auth/usuario',{ },{ params:form } )).data)
    
                    setTimeout(() => {
                        
                        resolve(data)
                        
                    }, 1500);
                } catch (error) {
    
                    reject(error)
                    
                }
            })
        },
        autenticar({ commit }, form){
            return new Promise(async (resolve,reject) => {
                try {
                    
                    const data = ((await axios.get('/auth/usuario',{ params:form } )).data)


                   if(!data.response) throw JSON.stringify(data.msg)
                   
                   console.log(data)
                   
                   setData(data.query, data.token)
                   
                    setTimeout(() => {
                        
                        resolve(data)
                        
                    }, 1500);
                } catch (error) {

                    clearData()
                    reject(error)
                    
                }
            })

        } 

    }
}