<template>
    <div>
        <v-tabs v-model="tab" centered>
        <v-tab  v-for="({ name, label }, i) in tabs"   :to="{ name }" :key="label+' '+i">
          {{ label }}
        </v-tab>
      </v-tabs>

  
                
            <v-container>

                <router-view></router-view>

            </v-container>

      
    </div>
</template>

<script>
export default {
    data() {
        return {

     tab:1,
     tabs:[
      {label:'acesso', name:'acesso'  },
      {label:'carregamento', name:'carregamentos'  },
      {label:'descarregamento', name:'descarregados'  }
    ]
    }
    },
    watch:{
        '$route.name':{
            handler(name){
                if(name == "historico"){
                    this.$router.push({ name: this.tabs[0].name })
                } 
            },
            immediate:true

        }
    },


    
}
</script>