import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/pages/HomeView.vue'
import Carregamento from '../views/pages/Carregamento.vue'
import Remoto from '../views/pages/RTF.vue'
import Biblioteca from '../views/pages/Historico.vue'
import Main from '../views/pages/Main.vue'
import Auth from '../views/pages/authentication'

import utils from '@/auth/utils'
const { setData, clearData,isUserLoggedIn } = utils

Vue.use(VueRouter)


const routes = [
  {
    path:"/auth",
    component: Auth,
 
    children:[
      {
        
        path:'cadastro',
        name:'cadastro',
        meta: { requiresAuth: false },
        component: ()=>import('../views/pages/authentication/Cadastro.vue'),
         
        },
        {
          path:'entrada',
          name:'entrada',
          meta: { requiresAuth: false },
          component: ()=> import('../views/pages/authentication/Entrada.vue'),
      },
  ]
    
  },
  {
    path: '/',
    redirect:"/home",
    component:Main,
    children:[
      {
        path: '/home',
        name: 'home',
        meta: { requiresAuth: true },
        component: HomeView
      },
      { 
        path: '/carregamento',
        name: 'carregamento',
        meta: { requiresAuth: true },
        component: Carregamento
      },
      {
        path: '/historico',
        name: 'historico',
        meta: { requiresAuth: true },
        component: Biblioteca,
        children:[
         
            {
              path:'carregamento',
              name:'carregamentos',
              meta: { requiresAuth: true },
              component: ()=>import('../views/pages/Historico/Carregamento.vue') 
            },
            {
              path:'acesso',
              name:'acesso',
              meta: { requiresAuth: true },
              component: ()=>import('../views/pages/Historico/Acesso.vue')
            },
            {
              path:'descarregados',
              name:'descarregados',
              meta: { requiresAuth: true },
              component: ()=>import('../views/pages/Historico/Descarregado.vue')
            }
        ]
      },
      {
        path: '/remoteFileTransfer',
        name: 'remoto',
        meta: { requiresAuth: true },
        component: Remoto
      },
      
    ]
  },

]



const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})


router.beforeEach((to, from, next) => {

  const needAuth = (to.matched.some(record => record.meta.requiresAuth))

   
  if (needAuth) {

    if (isUserLoggedIn()) {
      
      next()

    } else {

      next({name: 'entrada' })
    }

  } else {
    next()
   }


})

export default router
