<template>
    <v-container>


        <form
        @submit.prevent="carregar"
        enctype="multipart/form-data" class="mt-2 d-flex justify-center"> 
            
            
            <v-card width="700" >
            <v-img height="300" 
            class="white--text align-end"
            gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.7)"      
            lazy-src="@/assets/img/scene-251.jpg"
             src="@/assets/img/scene-251.jpg">

             <v-card-title class="display-1">
                {{ 'Carregar arquivo' }}
              </v-card-title>
                
            </v-img>
            
              <v-card-text>
                

                <v-file-input
                v-model="files"
                color="primary"
                counter
                multiple
                required
                :rules="[ v => !!v || 'campo vazio' ]"
                @change="prepararArquivos"
                placeholder="Selecione o arquivo"
                prepend-icon="mdi-paperclip"
                outlined
                :loading="loading"
                :disabled="loading"
                :show-size="1000"
              >
                <template v-slot:selection="{ index, text }">
                  <v-chip
                    v-if="index < 2"
                    color="primary"
                    dark
                    label
                    small >
                    {{ text }}
                  </v-chip>
            
                
                </template>
              </v-file-input>
              </v-card-text>

              <v-card-actions>
                <v-btn 
                :loading="loading"
                :disabled="loading"
                type="submit"
                block color="primary" dark>Carregar 
                    <v-icon right> mdi-cloud-upload </v-icon>
                </v-btn>
              </v-card-actions>
            </v-card>


            
            
          </form>
    </v-container>
</template>
<script>
    
    import { mapState,mapGetters,mapActions } from 'vuex'
    
    export default {
        data() {
            return {
                files: [],
                n:{
                snackbar:false,
                msg:'preparado para carregar arquivo'
              }
            }
        },
        computed: {
          ...mapGetters({
            loading:'carregamento/loading_state',
            selected:'carregamento/arquivosGetter',


          })
        },
        methods: {
            ...mapActions({
                mandarPreparar:'carregamento/prepararArquivos',
                carregarBackend:'carregamento/tentarCarregar',
                call:'assets/notificate'

            }), 
            async prepararArquivos(files){
                try {
                    
                  const  msg = await this.mandarPreparar(files)
                  this.n.msg = msg  
                  this.call({text:msg})

                  
                  // $store.state.carregamento.arquivos
                } catch (error) {
                  
                  this.call({text:error,color: '#e14343'})
 
                    
                }
            },
            async carregar(){
              try {
                
                const msg = (await this.carregarBackend())
                this.call({text:msg})

                
              } catch (error) {

                this.call({text:error,color: '#e14343'})

                
              }
             //   console.log(this.files)
            }
        },
      
        watch:{
            files:{
                handler:( files )=>{
                   // console.log(files)
                },
                immediate:false
            }
        },
        mounted() {

        },
    }

</script>